.layout-card {
    display: flex;
    gap: 30px;
    padding-top: 0;

    @media screen and (max-width: 767px) {
        flex-direction: column;
    }

    &__slider-container {
        position: relative;
        width: 68%;
        height: auto;

        @media screen and (max-width: 1239px) {
            width: 50%;
        }

        @media screen and (max-width: 991px) {
            height: auto;
        }

        @media screen and (max-width: 767px) {
            width: 100%;
        }
    }

    &__content-block {
        width: calc(32% - 30px);

        @media screen and (max-width: 1239px) {
            width: calc(50% - 30px);
        }

        @media screen and (max-width: 767px) {
            width: 100%;
        }
    }
}

.card-foto-slider {
    position: relative;
    display: flex;
    gap: 20px;

    @media screen and (max-width: 991px) {
        flex-direction: column-reverse;
        justify-content: flex-end;
    }

    @media screen and (max-width: 767px) {
        position: relative !important;
    }

    &__preview {
        width: 100%;
        max-width: 95px;
        height: 670px;
        min-height: 0;

        @media screen and (max-width: 991px) {
            max-width: 100%;
            padding: 0 16px;
            height: auto;
        }

        .swiper { 
            height: 100%;
        }
    }

    &__foto {
        height: 100%;
        width: 100%;
        min-width: 0;

        @media screen and (max-width: 991px) {
            height: auto;
        }
    }

    &__foto-link,
    &__preview-img-box {
        position: relative;
        display: block;

        &::before { 
            content: '';
            display: block;
            padding-bottom: calc(673 / 505 * 100%);
        }
    }

    &__preview-img-box {
        &::before {
            padding-bottom: calc(126 / 95 * 100%);
        }
    }

    &__foto-img,
    &__preview-img {
        display: block;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: center;
    }

    .swiper-button-prev,
    .swiper-button-next {
        width: 26px;
        height: 100%;
        background-color: #FFFFFF;
        margin-top: 0;
        top: 0;
        left: 0;

        @media screen and (min-width: 992px) {
           width: 100%;
           height: 36px;

           &::after {
            transform: rotate(90deg);
           }
        }
    }

    .swiper-button-next {
        left: auto;
        right: 0;

        @media screen and (min-width: 992px) {
            width: 100%;
            height: 36px;

            top: auto;
            bottom: 0;
         }
    }

    &__action {
        display: none;
        position: absolute;
        top: 17px;
        right: 15px;
        gap: 10px;
        margin-bottom: 20px;
        z-index: 2;

        @media screen and (max-width: 767px) {
            display: flex;
        }
    }

    &__like {
        display: flex;
        gap: 6px;
        cursor: pointer;

        &-svg {
            width: 22px;
            height: 22px;
            color: #ffffff;
        }

        &.active {
            .card-foto-slider__like {
                &-svg {
                    color: #D92322;
                }
            }
        }
    }
}

.card-info {
    &__action {
        display: flex;
        gap: 10px;
        margin-bottom: 20px;

        @media screen and (max-width: 767px) {
            display: none;
        }
    }

    &__like {
        display: flex;
        gap: 6px;
        font-size: 16px;
        cursor: pointer;

        &-svg {
            width: 22px;
            height: 22px;
            color: #ffffff;
        }

        &.active {
            .card-info__like {
                &-svg {
                    color: #D92322;
                }
            }
        }
    }

    &__tags {
        display: flex;
        flex-wrap: wrap;
        gap: 10px;
        margin-bottom: 20px;
    }

    &__title {
        text-align: left;
    }

    &__desc, &__art {
        display: block;
        font-size: 16px;
        color: #9D9B9B;
        margin-bottom: 10px;

        @media screen and (max-width: 767px) {
            font-size: 14px;
        }
    }

    &__art {
        margin-bottom: 20px;
    }

    &__price-block {
        margin-bottom: 20px;
        color: #D92322;
        font-size: 20px;
    }

    &__price-old {
        color: #9D9B9B;
        text-decoration: line-through;
        display: inline-block;
        margin-left: 10px;
    }

    &__price-saving {
        display: block;
        margin-top: 4px;
        font-size: 12px;
    }

    &__colors-box {
        margin-bottom: 26px;
    }

    &__size-select {
        max-width: 417px;
        margin-bottom: 10px;
    }

    &__table-size {
        display: inline-block;
        margin-bottom: 26px;
        font-size: 14px;
        border-bottom: 1px solid #301410;
        line-height: 25px;
        cursor: pointer;
    }

    &__button {
        display: block;
        max-width: 417px;
        text-align: center;
        margin-bottom: 26px;
    }

    &__delivery-point,
    &__delivery {
        display: flex;
        align-items: center;
        gap: 10px;
        min-height: 43px;
        margin-bottom: 10px;

        @media screen and (max-width: 767px) {
            font-size: 16px;
            gap: 6px;
            min-height: 36px;
        }
    }

    &__delivery-point-svg,
    &__delivery-svg {
        width: 24px;
        height: 24px;
    }

    &__delivery-point-text-accent {
        color: #0455C0;
    }

    &__delivery-desc {
        display: block;
        margin-bottom: 26px;
        font-size: 14px;
        color: #9D9B9B;

        @media screen and (max-width: 767px) {
            font-size: 12px;
        }
    }
}

.inform-block {
    &__item {
        margin-bottom: 20px;
        &:last-child {
            margin-bottom: 0;
        }
    }
    &__item-body {
        display: none;
        overflow: hidden;
        font-size: 16px;

        @media screen and (max-width: 767px) {
            font-size: 14px;
        }
    }

    &__item-title {
        display: flex;
        justify-content: space-between;
        align-items: center;
        gap: 10px;
        cursor: pointer;
        margin-bottom: 20px;

        &.open {
            .inform-block__item-arrow-svg {
                transform: rotate(90deg);
            }
        }
    }

    &__item-arrow-svg {
        width: 6px;
        height: 15px;
        transition: 0.3s;
        transform: rotate(-90deg);
    }
}

.inform-block-features {
    &__item {
        display: flex;
        gap: 15px;
        margin-bottom: 10px;

        &:last-child {
            margin-bottom: 0;
        }
    }

    &__title {
        width: 41%;
        color: #9D9B9B;

        @media screen and (max-width: 767px) {
            width: 49%;
        }
    }

    &__desc {
        width: 59%;

        @media screen and (max-width: 767px) {
            width: 51%;
        }
    }
}

.look-block {
    &__container  {
        display: flex;
        gap: 40px;
    }

    &__item {
        min-width: 0;
        flex-grow: 1;
        flex: calc((100% - 54px) / 3);
    }
}


.look-block-item {
    position: relative;
    overflow: hidden;
    height: 968px;

    &--height-min {
        height: 738px;
    }

    &__img {
        height: 100%;
        object-fit: cover;

    }

    &__points-container {
        position: absolute;
        height: 100%;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);

    }

    &__points {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 100%;
        height: 100%;
    }

    &__item {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
    }

    &__point {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 24px;
        height: 24px;
        border-radius: 50%;
        background-color: #ffffff;
        cursor: pointer;
        &:before {
            position: absolute;
            content: '';
            width: 16px;
            height: 16px;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            border: 1px solid #BEB1A6;
            border-radius: 50%;
        }

    }
}