.banner-block {
    margin-top: 40px;
    padding-top: 0;

    &__top {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 10px 20px;
        margin-bottom: 20px;

        @media screen and (max-width: 767px) {
            grid-template-columns: repeat(1, 1fr);
            margin-bottom: 10px;
        }
    }

    &__slider {
        @media screen and (max-width: 767px) {
            max-width: 94%;
        }
    }
}

.banner-item {
    position: relative;

    &::before {
        display: block;
        content: '';
        padding-bottom: calc((505 / 706) * 100%);
    }

    &__img {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        display: block;
        object-fit: cover;
    }

    &__content {
        position: absolute;
        bottom: 0;
        left: 0;
        padding: 40px;
        color: #ffffff;
        max-width: 555px;

        @media screen and (max-width: 767px) {
            padding: 20px;
        }
    }

    &__title-big {
        display: block;
        font-family: 'Mirra Auriolic';
        font-size: 100px;

        @media screen and (max-width: 1240px) {
            font-size: 50px;
        }
    }

    &--rtl {
        .banner-item {
            &__content {
                right: 0;
                left: auto;
                text-align: right;
            }
        }
    }
}

.promo-link {
    position: relative;
    display: flex;
    align-items: flex-end;
    justify-content: flex-end;
    text-decoration: none;

    &::before {
        display: block;
        content: '';
        padding-bottom: calc((250 / 465) * 100%);
    }

    &__img {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    &__text {
        position: relative;
        padding: 24px;
        color: #ffffff;
        font-size: 40px;

        @media screen and (max-width: 991px) {
            font-size: 26px;
        }
    }
}

.banner-block-slider {
    overflow: visible;
}

.catalog-tags-list {
    &__list {
        display: flex;
        gap: 20px;
        flex-wrap: wrap;
        justify-content: center;

        @media screen and (max-width: 767px) {
            position: relative;
            overflow: auto;
            flex-wrap: nowrap;
            gap: 10px;
            width: calc(100% + 30px);
            left: -15px;
            padding: 0 15px;
            justify-content: flex-start;

            &::-webkit-scrollbar {
                display: none;
            }
        }
    }
}