.overlay {
    display: none;
    // display: block;
    position: fixed;
    z-index: 1000;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background: rgba(51, 51, 51, 0.70);
}

.modals-container {
    display: none;
    // display: block;
    position: fixed;
    right: 0;
    top: 0;
	z-index: 1000;

    @media screen and (max-width: 767px) {
        display: block !important;
        opacity: 1 !important;
    }
	&__close {
        position: absolute;
        width: 40px;
        height: 40px;
        top: 16px;
        right: 550px;
        cursor: pointer;
        z-index: 1;
        &:before, &:after {
            position: absolute;
            content: '';
            top: 50%;
            left: 50%;
            width: 70%;
            height: 2px;
            background-color: #ffffff;
            transform: translate(-50%, -50%) rotate(45deg);
        }
        &:after {
            transform: translate(-50%, -50%) rotate(-45deg);
        }
    }
}

.modal {
    display: none;
    // display: block;
    position: fixed;
    bottom: 0;
    right: 0;
    max-width: 530px;
    width: 100%;
	height: 100vh;
    background-color: #ffffff;
    z-index: 1001;
    @media screen and (max-width: 767px) {
        display: block !important;
        opacity: 1 !important;
        height: auto;
        transition: 0.3s !important;
        overflow-y: auto;
        max-width: 100%;
        transform: translateY(100%);
        max-height: 98%;
        scrollbar-gutter: stable;

        &::-webkit-scrollbar {
            width: 12px;
            height: 12px;
        }
        
        &::-webkit-scrollbar-track {
            background: transparent;
        }
        
        &::-webkit-scrollbar-thumb {
            background: #F4EFE9;
            border-radius: 10px;
        }
    }

    &__swipe-close {
        display: none;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 35px;
        z-index: 10;


        &::after {
            content: '';
            position: absolute;
            top: 10px;
            left: 50%;
            transform: translateX(-50%);
            width: 40px;
            height: 5px;
            background-color: #9D9B9B;
            border-radius: 100px;
        }

        @media screen and (max-width: 767px) {
            display: block;
            position: sticky;
        }
    }

    &.active {
        @media screen and (max-width: 767px) {
            transform: translateY(0);
        }
    }
   
    &__container {
        height: 100vh;
        overflow-y: auto;
        scrollbar-gutter: stable;

        @media screen and (max-width: 767px) {
            overflow: hidden;
            height: auto;
            scrollbar-gutter: auto;
        }

        &::-webkit-scrollbar {
            width: 12px;
            height: 12px;
        }
        
        &::-webkit-scrollbar-track {
            background: transparent;
        }
        
        &::-webkit-scrollbar-thumb {
            background: #F4EFE9;
            border-radius: 10px;
        }
    }

    &__body {
        padding: 80px 35px 40px 60px;
        @media screen and (max-width: 767px) {
            padding: 0 3px 30px 15px;
        }
    }

    &__title {
        display: block;
        text-align: left;
    }
    
}

.modal-catalog-card {
	&__slider {
		position: relative;
		margin-bottom: 20px;
	}

	&__img-box {
		position: relative;
		background-color: #F8F8F8;
		&::before {
			display: block;
			content: '';
			padding-bottom: calc((500 / 410) * 100%);
		}
	}

	&__img {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		object-fit: cover;
		object-position: center;
		border-radius: 10px;
	}

	&__labels {
        position: absolute;
        top: 10px;
        left: 10px;
        display: flex;
        flex-wrap: wrap;
        gap: 10px;
        width: calc(100% - 65px);
		z-index: 1;
    }

    &__like {
        position: absolute;
        top: 10px;
        right: 10px;
		z-index: 1;
        cursor: pointer;

        &-svg {
            width: 40px;
            height: 40px;
            color: #ffffff;
        }

        &.active {
            .card-catalog-item__like {
                &-svg {
                    color: #D92322;
                }
            }
        }
    }

	&__slider-navigation {
		margin-top: 10px;
	}

	&__art {
		display: block;
		color: #9D9B9B;
		font-size: 16px;
		margin-bottom: 10px;
	}

	&__title {
		display: block;
		font-size: 32px;
		margin-bottom: 10px;
		min-height: 76px;
	}

	&__price-block {
        display: flex;
        gap: 10px;
        color: #D92322;
        font-size: 24px;
        margin-bottom: 10px;

        &:last-child {
            margin-bottom: 27px;
        }

        @media screen and (max-width: 767px) {
            font-size: 16px;

            &:last-child {
                margin-bottom: 15px;
            }
    
        }
    }

	&__price-old {
        color: #9D9B9B;
        text-decoration: line-through;
    }

    &__colors-box, 
    &__size-select {
        margin-bottom: 10px;
    }

    &__table-size {
        display: inline-block;
        margin-bottom: 40px;
        font-size: 14px;
        border-bottom: 1px solid #301410;
        line-height: 25px;
        cursor: pointer;
    }

    &__button-block {
        display: flex;
        align-items: center;
        gap: 20px;

        @media screen and (max-width: 767px) {
            flex-wrap: wrap;
        }
    }

    &__button {
        width: 100%;
    }
}

.modal-size {
    padding: 0 30px;
    
    table {
        width: 100%;
        border-collapse: collapse;
        color: #9D9B9B;
        font-size: 20px;
        font-weight: 400;
    }

    thead {
        color: #301410;
        font-size: 16px;
        th {
            padding: 10px;
        }
        tr {
            border-top: 1px solid #BEB1A6;
        }
    }
    
    th, td {
        text-align: center;
        padding: 30px 5px 21px 5px;
        font-weight: 400;
    }
    
    tr {
        border-bottom: 1px solid #BEB1A6;
    }
    
    th {
        background-color: #ffffff;
    }

}

.modal-filter-list {
    .filter-block {
        display: block;
    }
}