.page-contact {
    display: flex;
    gap: 30px 60px;

    @media screen and (max-width: 767px) {
        flex-direction: column;
        align-items: center;
    }

    &__img {
        width: 100%;
        max-width: 646px;
        min-width: 0;
        height: 100%;

        @media screen and (max-width: 767px) {
            width: calc(100% + 30px);
        }
    }

    &__title {
        text-align: left;
    }

    &__content {
        max-width: 700px;
        width: 100%;

        @media screen and (max-width: 767px) {
            font-size: 16px;
            line-height: 27px;
        }

        a {
            font-family: 'Montserrat';
            white-space: nowrap;

            @media screen and (max-width: 767px) {
                font-family: $font;
            }
        }

        h2 {
            margin: 40px 0;
        }
        
        p {
            @media screen and (max-width: 767px) {
                margin-bottom: 10px;

                &:last-child {
                    margin-bottom: 0;
                }
            }
        }
    }
}