.banner-catalog-block {
    margin-top: 40px;
    padding-top: 0;

    @media screen and (max-width: 991px)  {
        margin-top: 0;
    }

    &__container {
        display: flex;
        gap: 10px 20px;

        @media screen and (max-width: 991px) {
            flex-direction: column;
        }
    }

    &__slider {
        position: relative;
        width: calc(71% - 10px);
        @media screen and (max-width: 991px) {
            width: 100%;
        }
    }

    &__promo-link-block {
        width: calc(29% - 10px);
        display: flex;
        flex-direction: column;
        gap: 20px;

        @media screen and (max-width: 991px) {
            width: 100%;
            flex-direction: column;
            gap: 10px;
        }
    }

    &__promo {
        &::before {
            padding-bottom: calc((242.5 / 464) * 100%);
        }
    }

}

.banner-catalog-slider {

    &__container {
        height: 100%;
    }

    &__item {
        position: relative;
        height: 100%;
        &::before {
            display: block;
            content: '';
            padding-bottom: calc((505 / 1151) * 100%);

            @media screen and (max-width: 991px) {
                padding-bottom: calc((238 / 330) * 100%);
            }
        }

        &--rtl {
            .banner-catalog-slider {
                &__item-content {
                    right: 0;
                    left: auto;
                    text-align: right;
                }
            }
        }
    }

    &__item-img {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        display: block;
        object-fit: cover;
        object-position: top;
    }

    &__item-content {
        position: absolute;
        bottom: 0;
        left: 0;
        padding: 40px 40px 120px 40px;
        color: #ffffff;
        max-width: 555px;

        @media screen and (max-width: 767px) {
            padding: 20px 20px 50px 20px;
        }
    }

    &__item-title-big {
        display: block;
        font-family: 'Mirra Auriolic';
        font-size: 100px;

        @media screen and (max-width: 1240px) {
            font-size: 50px;
        }
    }

   

    &__navigation {
        position: absolute;
        width: 100%;
        left: 40px;
        bottom: 40px;
        height: 40px;

        @media screen and (max-width: 767px) {
            bottom: 20px;
            left: 20px;
            height: 8px;
        }

        .swiper-pagination {
            bottom: 0;
        }

        .swiper-button-prev,
        .swiper-button-next {
            border-radius: 50%;
            left: auto;
            right: 150px;
            bottom: 40px;
            width: 40px;
            height: 40px;
            background-color: #F8E8D8;
            @media screen and (max-width: 767px) {
                display: none;
            }
        }

        .swiper-button-next {
            right: 80px;
        }
    }
}

.slider-card-preview {
    &__container {
        display: flex;
        align-items: center;
        gap: 20px 140px;

        @media screen and (max-width: 1024px) {
            gap: 20px 40px;
        }

        @media screen and (max-width: 767px) {
            flex-direction: column;
        }
    }

    &__preview-block {
        border: 1px solid #301410;
        width: calc(64% - 70px);

        @media screen and (max-width: 767px) {
            width: 100%;
        }
    }

    &__cards-block {
        width: 277px;

        @media screen and (max-width: 767px) {
            width: 164px;
        }
    }

    &__preview-item {
        padding: 100px 138px 80px 138px;

        @media screen and (max-width: 1024px) {
            padding: 50px 65px 40px 65px;
        }

        @media screen and (max-width: 1024px) {
            padding: 40px 24px;
        }
    }

    &__preview-img-box {
        position: relative;
        margin-bottom: 100px;
        @media screen and (max-width: 1024px) {
            margin-bottom: 40px;
        }
        &::before {
            display: block;
            content: '';
            padding-bottom: calc((500 / 568) * 100%);

            @media screen and (max-width: 767px) {
                padding-bottom: calc((194 / 282) * 100%);
            }
        }
    }

    &__preview-img {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: contain;
        object-position: center;
    }

    &__preview-button {
        width: 100%;
    }

    &__navigation {
        margin-top: 40px;

        @media screen and (max-width: 767px) {
            margin-top: 20px;

            .swiper-button-prev,
            .swiper-button-next {
                display: none;
            }
        }
    }
}