.header {
    position: fixed;
    width: 100%;
    top: 0;
    left: 0;
    background-color: #ffffff;
    z-index: 100;

    &__menu-mobi {
        display: none;
        height: calc(100vh - 77px);
        position: absolute;
        top: 76px;
        left: 100%;
        overflow: auto;
        transition: 0.3s left;
        z-index: 1000;
        width: 100%;
        &.open {
            left: 0;
        }

        @media screen and (max-width: 991px) {
            display: block;
        }
    }

    .header-top__subscription-block {
        overflow: hidden;
        height: 0;
    }

    .header-top__menu-block {
        height: auto;
    }

    .header-bottom__container {
        transition: 0.3s height;
        height: 60px;
        overflow: hidden;
    }

    &.sticky-header {
        .header-top__subscription-block {
            height: auto;
        }

        .header-top__menu-block {
            overflow: hidden;
            height: 0;
        }

        .header-bottom__container {
            height: 0;
        }

        .header-middle__container {
            padding: 15px 0 25px 0;
        }

        .header-middle__search {
            display: none;
        }

        .header-middle__col:first-child {
            flex-grow: 1;
        }

        .header-middle__menu {
            width: 100%;
            justify-content: center;
        }
    }
}

.header-top {
    background-color: #F8E8D8;

    @media screen and (max-width: 991px) {
        display: none;
    }

    &__subscription-block {
        display: flex;
        align-items: center;
        justify-content: center;
    }

    &__subscription-button {
        cursor: pointer;
    }

    &__container {
        min-height: 46px;
        padding: 10px 0;
    }

    &__menu-block {
        display: flex;
        justify-content: space-between;
        align-items: center;
        gap: 20px;
    }

    &__menu {
        display: flex;
        gap: 20px;
        justify-content: space-between;
        width: 627px;
        max-width: 100%;
    }

    &__menu-item {
        padding-left: 0;
        margin: 0;
        &::before {
            display: none;
        }
    }

    &__menu-link, &__contact-item {
        font-size: 14px;
        text-decoration: none;
    }

    &__menu-link {
        &.active {
            border-bottom: 1px solid currentColor;
        }
    }

    &__contact {
        display: flex;
        gap: 40px;
    }
}

.header-middle {
    border-bottom: 1px solid #F4EFE9;

    &__container {
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 20px;
        min-height: 105px;
        padding: 35px 0 20px 0;

        
        @media screen and (max-width: 991px) {
            padding: 10px 0;
            min-height: 76px;
        }
    }

    &__burger {
        display: none;

        @media screen and (max-width: 991px) {
           display: block;
        }
    }

    &__logo {
        min-width: 159px;
        max-width: 100%;

        img {
            display: block;
            width: 100%;
        }

        @media screen and (max-width: 991px) {
            min-width: 100px;
        }
    }

    &__col {
        display: flex;
        align-items: center;
        justify-content: space-between;

        &:first-child {
            gap: 40px;

            @media screen and (max-width: 991px) {
                gap: 14px;
            }
        }
        &:last-child {
            gap: 32px;
            min-width: 0;
        }
    }

    &__menu {
        display: flex;
        gap: 20px;
        align-items: center;
        justify-content: space-between;

        @media screen and (max-width: 991px) {
            display: none;
        }
    }

    &__menu-item {
        padding: 0;
        margin: 0;

        &::before {
            display: none;
        }
    }

    &__menu-link {
        font-size: 16px;
        text-decoration: none;

        &.active {
            border-bottom: 1px solid currentColor;
        }
    }

    &__search {
        width: 691px;
        max-width: 100%;

        @media screen and (max-width: 991px) {
            display: none;
        }
    }

    &__user-icons {
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 20px;
        padding: 10px 13px;
    }

    &__user-icon {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 24px;
        height: 24px;
        text-decoration: none;
        &-svg {
            width: 24px;
            height: 24px;
        }

        &--search-mobi {
            display: none;

            @media screen and (max-width: 991px) {
                display: block;
            }
        }

        &--like-desk {
            @media screen and (max-width: 991px) {
                display: none;
            }
        }
    }
}

.header-bottom {

    @media screen and (max-width: 991px) {
        display: none;
    }

    &__container {
        display: flex;
        align-items: center;
        justify-content: space-between;
        height: 60px;
    }

    &__menu {
        display: flex;
        gap: 40px;
        padding: 8px 0;
    }

    &__menu-item {
        padding: 0;
        margin: 0;

        &::before {
            display: none;
        }
    }

    &__menu-link {
        font-size: 16px;
        text-decoration: none;

        &.active {
            border-bottom: 1px solid currentColor;
        }
    }
}

.search {
    position: relative;

    &__input {
        width: 100%;
        padding: 13px 77px 13px 17px;
        font-size: 16px;
        font-family: $font;
        border: 1px solid #F4EFE9;
        border-radius: 36px;
        background-color: #ffffff;
        outline: none;

        &::placeholder {
            color: #BEB1A6;
        }
    }

    &__button {
        position: absolute;
        right: 1px;
        top: 1px;
        width: 63px;
        height: calc(100% - 2px);
        background-color: #F8E8D8;
        color: #301410;
        transition: 0.3s;
        border-radius: 36px;
        cursor: pointer;
        border: none;
        outline: none;

        &:hover {
            background-color: #D9C9BA;
        }
    }

    &__icon {
        width: 24px;
        height: 24px;
    }
}

.burger {
    position: relative;
    min-width: 36px;
    width: 36px;
    height: 36px;
    cursor: pointer;
    &__line {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 19px;
        height: 2px;
        border-radius: 10px;
        background-color: #301410;
        transition: .3s ease;
        &:before,
        &:after {
            content: '';
            position: absolute;
            width: 100%;
            height: 100%;
            left: 50%;
            transform: translateX(-50%);
            top: -7px;
            border-radius: 10px;
            background-color: #333333;
            transition: .3s ease;
        }
        &:after  {
            top: auto;
            bottom: -7px;
        }
    }
    &.open {
        .burger {
            &__line {
                background-color: transparent;
                &:before {
                    top: 50%;
                    transform: translate(-50%, -50%) rotate(45deg);
                }
                &:after {
                    top: 50%;
                    transform: translate(-50%, -50%) rotate(-45deg);
                }
            }
        }
    }
}

.menu-mobi {
    width: 100%;
    padding: 10px 15px;
    background-color: #ffffff;
    min-height: 100%;

    &__menu {
        margin-bottom: 30px;
    }

    &__item {
        width: 100%;
        padding: 0;
        margin: 0;

        &::before {
            display: none;
        }
    }

    &__item-title {
        position: relative;
        padding: 10px 15px;
        font-size: 16px;
        text-decoration: none;
        color: #260B00;

        &--trigger {
            cursor: pointer;
        }

        &.open {
            .menu-mobi {
                &__arrow {
                    transform: translateY(-50%) rotate(90deg);
                }
            }
        }
    }

    &__item-block {
        display: none;
        overflow: hidden;
        .menu-mobi {
            &__list {
                padding: 20px 15px 16px 15px;
            }
        }
    }

    &__elem-lvl1, &__elem-lvl2  {
        color: #260B00;
        text-decoration: none;

        &:hover {
            color: #260B00;
        }

        &.active {
            border-bottom: 1px solid currentColor;
        }
    }

    &__item-content {
        width: 100%;
        border-bottom: 1px solid #F4EFE9;

        .menu-mobi {
            &__item {
                margin-bottom: 14px;
                
                &:last-child {
                    margin-bottom: 0;
                }
            }
        }
    }

    &__arrow {
        position: absolute;
        top: 50%;
        right: 15px;
        transform: translateY(-50%) rotate(270deg);
        width: 24px;
        height: 24px;
        display: flex;
        align-items: center;
        justify-content: center;
        transition: 0.3s;

        &-svg {
            width: 10px;
            height: 18px;
        }
    }

    &__contact-item {
        display: block;
        font-size: 20px;
        text-decoration: none;
        margin-bottom: 10px;

        &:last-child {
            margin-bottom: 0;
        }
    }
}