.breadcrumbs {
    padding: 40px 0;
    overflow: auto;

    @media screen and (max-width: 767px) {
        padding: 20px 0 30px 0;
    }

    &::-webkit-scrollbar {
        display: none;
    }

    &__container {
        display: flex;
        justify-content: center;
        align-items: center;
        width: fit-content;
        margin: 0 auto;
    }

    &__item {
        position: relative;
        text-decoration: none;
        font-size: 14px;

        @media screen and (max-width: 767px) {
            font-size: 12px;
        }

        &:not(:first-child) {
            margin-left: 40px;

            &::before {
                content: '';
                position: absolute;
                top: 50%;
                left: -20px;
                transform: translateY(-50%);
                width: 1px;
                height: 17px;
                background-color: #9D9B9B;
            }
            
        }
    }
}