.page-404 {
    margin-top: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 30px 50px;

    @media screen and (max-width: 767px) {
        flex-direction: column;
    }

    &__content {
        max-width: 513px;
        width: 100%;
    }

    &__img {
        width: 100%;
        max-width: 400px;
        min-width: 0;
        height: 100%;

        @media screen and (max-width: 767px) {
            width: calc(100% + 30px);
        }
    }

    &__title {
        font-size: 44px;
        font-weight: 700;
        margin-bottom: 16px;

        @media screen and (max-width: 767px) {
            font-size: 32px;
        }
    }

    &__text {
        display: block;
        font-size: 16px;
        margin-bottom: 40px;

        @media screen and (max-width: 767px) {
            font-size: 14px;
        }
    }

    &__link {
        text-decoration: none;
    }

    &__button {
        width: 417px;
        max-width: 100%;
    }
}