.card-catalog-item {
    position: relative;
    min-width: 164px;
    cursor: pointer;

    &__image-block {
        position: relative;
        width: 100%;
        background-color: #F8F8F8;
        &::before {
            display: block;
            content: '';
            padding-bottom: calc((368 / 277) * 100%);
        }
    }

    &__img {
        position: absolute;
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
        border-radius: 10px;
        object-fit: cover;
        object-position: center;
    }

    &__labels {
        position: absolute;
        top: 10px;
        left: 10px;
        display: flex;
        flex-wrap: wrap;
        gap: 10px;
        width: calc(100% - 50px);
    }

    &__like {
        position: absolute;
        top: 10px;
        right: 10px;
        cursor: pointer;

        &-svg {
            width: 22px;
            height: 22px;
            color: #ffffff;
        }

        &.active {
            .card-catalog-item__like {
                &-svg {
                    color: #D92322;
                }
            }
        }
    }

    &__content-block {
        padding: 20px;

        background-color: #ffffff;

        @media screen and (max-width: 767px) {
            padding: 5px 10px;
        }
    }

    &__art {
        display: block;
        margin-bottom: 5px;
        color: #9D9B9B;
        font-size: 16px;

        @media screen and (max-width: 767px) {
            font-size: 14px;
        }
    }

    &__title {
        display: block;
        margin-bottom: 5px;
        font-size: 18px;

        @media screen and (max-width: 767px) {
            font-size: 14px;
        }
    }

    &__price-block {
        display: flex;
        gap: 10px;
        color: #D92322;
        font-size: 18px;
        margin-bottom: 10px;

        &:last-child {
            margin-bottom: 27px;
        }

        @media screen and (max-width: 767px) {
            font-size: 16px;

            &:last-child {
                margin-bottom: 15px;
            }
    
        }
    }

    &__price-old {
        color: #9D9B9B;
        text-decoration: line-through;
    }

    &__size-block {
        opacity: 0;
        display: flex;
        gap: 16px;
        font-size: 12px;
        color: #9D9B9B;
        transition: 0.3s opacity;
    }

    &:hover {
        .card-catalog-item {
            &__size-block {
                opacity: 1;
            }
        }
    }
}

.card-mini-catalog-item {
    position: relative;

    &__image-block {
        position: relative;
        width: 100%;
        background-color: #F8F8F8;
        margin-bottom: 10px;
        &::before {
            display: block;
            content: '';
            padding-bottom: calc((368 / 277) * 100%);
        }
    }

    &__img {
        position: absolute;
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
        border-radius: 10px;
        object-fit: cover;
        object-position: center;
    }

    &__price-block {
        text-align: center;
        font-size: 16px;
        color: #9D9B9B;
    }
}