.blog-tags {
    display: flex;
    gap: 18px;
    overflow: auto;
    margin: 60px 0 40px 0;

    &::-webkit-scrollbar {
        display: none;
    }
}

.blog-list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 40px;
    margin-bottom: 80px;

    @media screen and (max-width: 767px) {
        grid-template-columns: repeat(2, 1fr);
        gap: 24px 2px;
        margin-bottom: 50px;
    }
}

.blog-card {
    text-decoration: none;
    color: #260B00;
    font-family: 'Gotham';

    @media screen and (max-width: 767px) {
        font-family: $font;
    }
    
    &:hover {
        color: #260B00;
    }
    &__img {
        display: block;
        width: 100%;
        margin-bottom: 20px;

        @media screen and (max-width: 767px) {
            margin-bottom: 10px;
        }
    }

    &__tag {
        display: block;
        margin-bottom: 20px;
        font-size: 16px;
        color: #868686;

        @media screen and (max-width: 767px) {
            font-size: 12px;
            margin-bottom: 10px;
        }
    }

    &__title {
        display: block;
        font-size: 24px;
        margin-bottom: 20px;

        @media screen and (max-width: 767px) {
            font-size: 18px;
            margin-bottom: 10px;
        }
    }

    &__more {
        position: relative;
        display: inline-block;
        color: #545454;
        border-bottom: 1px solid #260B00;

        @media screen and (max-width: 767px) {
            font-size: 18px;
            border-bottom: none;
        }

        &::before {
            content: '';
            position: absolute;
            top: 50%;
            right: -15px;
            width: 9px;
            height: 9px;
            border-bottom: 1px solid currentColor;
            border-right: 1px solid currentColor;
            transform: translateY(-50%) rotate(-45deg);
        }
    }
}