@import 'variables.scss';

html {
    height: 100%;
    margin: 0;
    padding: 0;
    overflow-x: hidden;
    -webkit-text-size-adjust: none;
    scroll-behavior: smooth;

    &.fix {
        overflow: hidden;
    }
}

body {
    font-family: $font;
    font-size: 18px;
    line-height: normal;
    color: #301410;
    position: relative;
    display: flex;
    flex-direction: column;
    width: 100%;
    min-height: 100vh;
    margin: 0 auto;
    overflow: hidden;

    @media screen and (max-width: 768px) {
        font-size: 14px;
    }

    *,
    :before,
    :after {
        box-sizing: border-box;
    }

    *:focus {
        outline: 0 !important;
    }

    * {
        -webkit-tap-highlight-color: rgba(0, 0, 0, 0);;
    }
}

main {
    padding-top: 213px;
    position: relative;
    flex: 1 0 auto;
    // overflow: hidden;

    @media screen and (max-width: 991px) {
        padding-top: 76px;
    }
}

.container {
    max-width: 1472px;
    width: 100%;
    margin: 0 auto;
    position: relative;
    padding: 0 20px;

    @media screen and (max-width: 1500px) {
        max-width: 1240px;
    }

    @media screen and (max-width: 1240px) {
        max-width: 1100px;
    }

    @media screen and (max-width: 768px) {
        padding: 0 15px;
    }

    &--size-l {
        max-width: 1675px;
    }
    &--size-xl {
        max-width: 1760px;
    }
}

.section {
    padding: 50px 0;
    @media screen and (max-width: 768px) {
        padding: 25px 0;
    }

    &--theme-bg-light {
        padding: 100px 0;
        margin: 50px 0;
        background-color: #F8E8D8;

        @media screen and (max-width: 768px) {
            padding: 40px 0;
        }
    }
}

.button {
    display: inline-flex;
    min-width: 180px;
    justify-content: center;
    align-items: center;
    padding: 13px 30px 13px 30px;
    border-radius: 40px;
    transition: .3s ease;
    background-color: #301410;
    border: 1px solid #301410;
    color: #ffffff;
    font-family: $font;
    font-size: 16px;
    text-decoration: none;
    cursor: pointer;

    &:hover {
        background-color: #591A00;
        border: 1px solid #591A00;
        color: #ffffff;
    }

    @media screen and (max-width: 768px) {
        font-size: 14px;
    }

    &--theme-light {
        background-color: #BEB1A6;
        border: 1px solid #BEB1A6;

        &:hover {
            background-color: #591A00;
            border: 1px solid #591A00;
            color: #ffffff;
        }
    }
}

h1 {
    margin: 0 0 40px 0;
    font-size: 40px;
    font-weight: 400;
    @media screen and (max-width: 768px) {
        margin: 0 0 20px 0;
        font-size: 28px;
    }
    &:last-child {
        margin-bottom: 0;
    }
}

h2, .h2 {
    margin: 0 0 40px 0;
    font-size: 32px;
    font-weight: 400;
    text-align: center;
    @media screen and (max-width: 768px) {
        font-size: 24px;
        margin: 0 0 20px 0;
    }
    &:last-child {
        margin-bottom: 0;
    }
}

h3, .h3 {
    margin: 0 0 20px 0;
    font-size: 24px;
    font-weight: 400;
    text-align: center;
    @media screen and (max-width: 768px) {
        font-size: 20px;
        margin: 0 0 10px 0;
    }
    &:last-child {
        margin-bottom: 0;
    }
}

p {
    margin: 0 0 20px 0;
    &:last-child {
        margin-bottom: 0;
    }
}

a, .link {
    color: #D92322;
    transition: .3s ease;
    &:hover {
        color: #FF6F59;
    }

    &.link {
        &--theme-dark {
            color: #301410;
    
            &:hover {
                color: #A66851;
            }
        }

        &--theme-gray {
            color: #9D9B9B;
    
            &:hover {
                color: #301410;
            }
        }

        &--theme-dark-brown {
            color: #66342D;
    
            &:hover {
                color: #301410;
            }
        }

        &--theme-brown {
            color: #A66851;
    
            &:hover {
                color: #301410;
            }
        }

        &--theme-light-brown {
            color: #988A88;
    
            &:hover {
                color: #301410;
            }
        }

        &--theme-blue {
            color: #0455C0;
    
            &:hover {
                color: #031c3d;
            }
        }

        &--bordered {
            border-bottom: 1px solid currentColor;
        }
    }

    
}

ul {
    margin: 0;
    padding: 0;
    list-style: none;
    li {
        position: relative;
        display: flex;
        align-items: center;
        padding-left: 36px;
        margin-bottom: 20px;
        min-height: 20px;
        line-height: 150%;
        &:last-child {
            margin-bottom: 0
        }

        &:before {
            content: '';
            position: absolute;
            top: 10px;
            left: 10px;
            width: 10px;
            height: 10px;
            border-radius: 50%;
            background-color: #D92322;
        }
    }
}

.iti {
    --iti-path-flags-1x: url('../img/image/inputTel/flags.png');
    --iti-path-flags-2x: url('../img/image/inputTel/flags@2x.png');
    --iti-path-globe-1x: url('../img/image/inputTel/globe.png');
    --iti-path-globe-2x: url('../img/image/inputTel/globe@2x.png');
}

