.checkbox {
    display: block;
    width: 20px;
    height: 20px;
    cursor: pointer;
    input {
        position: relative;
        appearance: none;
        position: relative;
        border: 1px solid #F4EFE9;
        width: 100%;
        height: 100%;
        border-radius: 50%;
        background: #ffffff;
        cursor: pointer;
        margin: 0;
        transform: 0.3s;
        

        &:after {
            content: '';
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            width: 20px;
            height: 20px;
            background-image: url('../img/image/check-icon.svg');
            background-position: center;
            background-size: contain;
            background-repeat: no-repeat;
            transform: 0.3s;
            opacity: 0;
        }

        &:checked {
            background: #301410;
            border: 1px solid #301410;
            &:after {
                opacity: 1;
            }
        }
        
    }
}

.title-page {
    margin-bottom: 40px;
    text-align: center;
}

.tag {
    padding: 20px 30px;
    border-radius: 40px;
    background-color: #F8E8D8;
    text-decoration: none;
    color: #301410;
    transition: 0.3s;
    font-size: 16px;
    white-space: nowrap;

    &:hover {
        background-color: #D9C9BA;
        color: #301410;
    }

    &.active {
        background-color: #301410;
        color: #FFFFFF;
    }

    @media screen and (max-width: 767px) {
        padding: 20px 15px;
        font-size: 14px;
    }
}

.pagination {
    display: flex;
    gap: 14px;

    &__num {
        position: relative;
        font-size: 16px;
        text-decoration: none;

        &--active {
            padding-right: 45px;

            &::before {
                content: '';
                position: absolute;
                top: 45%;
                right: 0;
                height: 2px;
                width: 40px;
                border-radius: 2px;
                background-color: currentColor;
                transform: translateY(-50%);
            }
        }
    }

    &__arrow {
        width: 24px;
        height: 24px;
        display: flex;
        align-items: center;
        justify-content: center;
        text-decoration: none;
        color: currentColor;

        &:hover {
            color: #63423D;
        }

        &-svg {
            width: 10px;
            height: 18px;
        }

        &--left {
            margin-right: 24px;
        }

        &--right {
            margin-left: 24px;

            transform: rotate(180deg);
        }
    }
}

.pagination-more {
    min-width: 323px;
}

.head-block {
    margin-bottom: 40px;

    @media screen and (max-width: 767px) {
        margin-bottom: 20px;
    }

    &:last-child {
        margin-bottom: 0;
    }

    &__title {
        margin-bottom: 10px;

        @media screen and (max-width: 767px) {
            margin-bottom: 6px;
        }

        &:last-child {
            margin-bottom: 0;
        }
    }

    &__link {
        display: block;
        margin: 0 auto;
        width: fit-content;
        text-align: center;
        text-decoration: none;
    }
}

.card-label {
    position: relative;
    padding: 4px 22px;
    border-radius: 20px;
    color: #ffffff;
    font-size: 12px;
    background-color: #BEB1A6;
    width: fit-content;

    &::before,
    &::after {
        content: '';
        position: absolute;
        top: 50%;
        left: 8px;
        transform: translateY(-50%);
        width: 8px;
        height: 8px;
        background-image: url('../img/image/sticker_1.svg');
        background-position: center;
        background-repeat: no-repeat;
    }

    &::after {
        left: auto;
        right: 8px;
    }

    &--sale-percentage {
        background-color: #66342D;
        &::before,
        &::after {
            background-image: url('../img/image/sticker_2.svg');
        }
    }

    &--profit {
        color: #301410;
        background-color: #F8E8D8;
        &::before,
        &::after {
            background-image: url('../img/image/sticker_3.svg');
        }
    }

    &--sale {
        color: #301410;
        background-color: #FFD4AA;
        &::before,
        &::after {
            background-image: url('../img/image/sticker_4.svg');
        }
    }
    &--size-l {
        padding: 7.5px 18px;
    }
}

.slider-pagination {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 27px;

    .swiper-button-prev,
    .swiper-button-next,
    .swiper-pagination {
        position: relative;
        width: fit-content;
        left: auto;
        right: auto;
        margin-top: 0;
        top: 0;
    }
}

.swiper-button-prev,
.swiper-button-next {
    --swiper-navigation-size: 18px;
    --swiper-theme-color: #000000;
    font-weight: 700;
    outline: none;
    user-select:none;
}

.catalog-slider {
    &__slider {
        .swiper {
            @media screen and (max-width: 767px) {
                overflow: visible;
            }
        }
    }

    &__slider-container {
        @media screen and (max-width: 767px) {
            max-width: 50%;
        }
    }

    &__navigation {
        margin-top: 40px;

        @media screen and (max-width: 767px) {
            .swiper-button-prev,
            .swiper-button-next {
                display: none;
            }
        }
    }
}

.catalog-mini-card-slider {
    &__slider {
        .swiper {
            @media screen and (max-width: 767px) {
                overflow: visible;
            }
        }
    }

    &__slider-container {
        max-width: 1082px;
        margin: 0 auto;
        @media screen and (max-width: 767px) {
            margin: 0;
            max-width: 83%;
        }
    }

    &__navigation {
        margin-top: 40px;

        @media screen and (max-width: 767px) {
            .swiper-button-prev,
            .swiper-button-next {
                display: none;
            }
        }
    }
}


.swiper-pagination {
    --swiper-pagination-bullet-horizontal-gap: 7px;
    --swiper-pagination-bullet-inactive-opacity: 1;
    --swiper-pagination-color: #301410;
    --swiper-pagination-bullet-inactive-color: #F8E8D8;
    --swiper-pagination-bullet-size: 10px;

    @media screen and (max-width: 767px) {
        --swiper-pagination-bullet-size: 8px;
        --swiper-pagination-bullet-horizontal-gap: 5px;
    }

    &--theme-accent {
        .swiper-pagination-bullet {
            &:before {
                border: 1px solid #301410;
            }
           
        }
        .swiper-pagination-bullet-active {
            &:before {
                border: 1px solid transparent;
            }
        }
    }
}

.swiper-pagination-bullets {
    display: flex;
    align-items: center;
}

.swiper-pagination-bullet {
    background-color: transparent;
    width: fit-content;
    height: fit-content;
    position: relative;
    display: flex;
    align-items: center;

    &:before {
        content: '';
        display: block;
        width: var(--swiper-pagination-bullet-size);
        height: var(--swiper-pagination-bullet-size);
        background-color: var(--swiper-pagination-bullet-inactive-color);
        border-radius: 50%;
    }

    &:after {
        display: block;
        content: '';
        width: 0;
        height: 2px;
        background-color: var(--swiper-pagination-color);
        border-radius: 2px;
        transition: 0.3s width;
    }

    &.swiper-pagination-bullet-active {

        &:before {
            background-color: var(--swiper-pagination-color);
        }

        &:after {
            width: 20px;
            margin-left: 6px;
        }
    }
}

.colors-box {
    &__list {
        display: flex;
        flex-wrap: wrap;
        gap: 10px;
        align-items: center;
        margin-bottom: 10px;
    }

    &__input {
        --var-color: #ffffff;
        position: relative;
        width: 28px;
        height: 28px;
        input {
            appearance: none;
            position: relative;
            border: 1px solid #FFFFFF;
            width: 100%;
            height: 100%;
            border-radius: 50%;
            background: var(--var-color);
            cursor: pointer;
            margin: 0;

            &:checked {
                border: 1px solid #301410;
            }
        }
    }

    &__label {
        font-size: 18px;

        @media screen and (max-width: 767px) {
            font-size: 14px;
        }
    }

    &__value {
        color: #BEB1A6;
    }
}

.size-select {
    position: relative;

    &__input-wrap {
        position: relative;

        &::after {
            content: '';
            position: absolute;
            top: 50%;
            transform: translateY(-50%) rotate(180deg);
            right: 25px;
            width: 12px;
            height: 6px;
            background-image: url('../img/image/arrow-icon.svg');
            background-position: center;
            background-repeat: no-repeat;
            background-size: cover;
            transition: 0.3s;
        }
    }

    &__input {
        position: relative;
        width: 100%;
        padding: 13px 45px 13px 25px;
        background-color: #F8F8F8;
        border: 1px solid #F4EFE9;
        border-radius: 40px;
        outline: none;
        font-family: $font;
        font-size: 16px;
        cursor: pointer;

        &::placeholder {
            color: #301410;
        }

       
    }

    &__dropdown {
        position: absolute;
        width: 100%;
        top: calc(100% + 14px);
        max-height: 320px;
        border-radius: 40px;
        border: 1px solid #F4EFE9;
        background-color: #F8F8F8;
        padding: 26px 25px;
        overflow: auto;
        transition: 0.3s;
        opacity: 0;
        pointer-events: none;
        max-height: 0;
        margin-bottom: 20px;

        &::-webkit-scrollbar {
            display: none;
        }

        &.active {
            opacity: 1;
            pointer-events: all;
            max-height: 320px;
        }
    }

    &__item {
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 10px;
        margin-bottom: 10px;
        padding: 5px 10px;
        font-size: 14px;
        background-color: transparent;
        transition: 0.3s;
        border-radius: 40px;
        cursor: pointer;

        &:hover {
            background-color: #F8E8D8;
        }

        &-title {
            &--light {
                color: #9D9B9B;
            }
        }

        &-bell {
            width: 16px;
            height: 16px;
            cursor: pointer;

            &-svg {
                width: 16px;
                height: 16px;
            }
        }
    }

    &--theme-white {
        .size-select {
            &__input {
                background-color: #ffffff;
            }
        }
    }
    &--min-dropdown {
        .size-select {
            &__dropdown {
                max-height: 150px;
        
                &.active {
                    opacity: 1;
                    pointer-events: all;
                    max-height: 150px;
                }
            }
        
        }
    }
}

.catalog-layout {
    &__container {
        display: flex;
        gap: 20px;

        @media screen and (max-width: 991px) {
            flex-direction: column;
        }
    }

    &__body {
        display: flex;
        flex-wrap: wrap;
        gap: 30px 26px;
        flex: calc(100% - 231px);
        max-width: calc(100% - 231px);

        @media screen and (max-width: 991px) {
            flex: 100%;
            max-width: 100%;
        }
        
        @media screen and (max-width: 767px) {
            gap: 20px 2px;
        }
    }

    &__filter-param {
        width: 100%;
        margin-bottom: 10px;

        @media screen and (max-width: 767px){
            margin-bottom: 0;
        }
    }

    &__card-item {
        flex: 0 0 calc((100% - 78px) / 4);
        max-width: calc((100% - 78px) / 4);

        @media screen and (max-width: 1240px) {
            flex: 0 0 calc((100% - 26px) / 2);
            max-width: calc((100% - 26px) / 2);
        }

        @media screen and (max-width: 776px) {
            flex: 0 0 calc((100% - 2px) / 2);
            max-width: calc((100% - 2px) / 2);
        }
    }

    &__filter {
        width: 211px;
    }

    &__featured {
        margin: 0 0 30px 0;
        position: relative;
        padding: 40px 0 40px 32px;
        background-color: #F8E8D8;
        flex: 0 0 100%;
        max-width: 100%;

        @media screen and (max-width: 776px) {
            position: relative;
            padding: 40px 0;
        }

        &::before {
            content: '';
            position: absolute;
            width: calc(100% + 100vw);
            height: 100%;
            left: 0;
            top: 0;
            background-color: #F8E8D8;
            z-index: -1;

            @media screen and (max-width: 776px) {
                left: -15px;
            }
        }

        &::after {
            content: '';
            position: absolute;
            width: calc(100% + 100vw);
            height: 100%;
            right: 100%;
            top: 0;
            background-color: #ffffff;
            z-index: 1;
            @media screen and (max-width: 776px) {
                display: none;
            }
        }
    }

    &__pagination {
       width: 100%;
    }

    &__viewed-products {
        width: 100%;

        padding: 30px 0;
    }

    &__content {
        display: flex;
        gap: 0 50px;

        @media screen and (max-width: 767px) {
            flex-direction: column;
        }
    }

    &__content-title {
        text-align: left;
        flex: 0 0 229px;
        min-width: 0;

        @media screen and (max-width: 767px) {
            flex: 0;
        }
    }

    &__no-result {
        width: 100%;
        margin-bottom: 30px;
        margin-top: 30px;

        @media screen and (max-width: 776px) {
            margin-bottom: 0;
        }
    }
}

.catalog-featured {
    &__slider-container {
        width: 100%;
    }

    &__slider {
        overflow: visible;
        @media screen and (max-width: 776px) {
            overflow: hidden;
        }
    }

    &__navigation {
        margin-top: 40px;

        @media screen and (max-width: 776px) {
            display: none;
        }
    }
}

.category-navigation {
    &__container {
        position: relative;
        display: flex;
        align-items: center;
        gap: 40px;

        @media screen and (max-width: 767px) {
            gap: 20px;
            width: 100vw;
            left: -15px;
            padding-left: 15px;
        }
    }

    &__slide  {
        width: fit-content;

        @media screen and (max-width: 767px) {
            &:last-child {
                padding-right: 15px;
            }
        }
    }

    &__main-category,
    &__item-category {
        position: relative;
        display: inline-block;
        padding: 6px 15px;
        border-radius: 20px;
        border: 1px solid #301410;
        color: #301410;
        text-decoration: none;
        transition: 0.3s;
        font-size: 14px;

        white-space: nowrap;
        &:hover {
            background-color: #301410;
            color: #ffffff;
        }
    }

    &__main-category,
    &__item-category--active {
        background-color: #301410;
        color: #ffffff;
    }

    &__main-category {
        &::after {
            content: '';
            position: absolute;
            height: 100%;
            width: 1px;
            background-color: #301410;
            right: -20px;
            top: 0;

            @media screen and (max-width: 767px) {
                right: -10px;
            }
        }
    }

    &__item-category {
        width: fit-content;
    }

    &__category-list {
        position: relative;
        flex-grow: 1;
        min-width: 0;
        display: flex;
        align-items: center;
        gap: 20px;
    }

    .swiper-button-prev,
    .swiper-button-next {
        --swiper-navigation-size: 11px;
        position: relative;
        width: 30px;
        min-width: 30px;
        height: 30px;
        border-radius: 50%;
        border: 1px solid #301410;
        margin-top: 0;
        top: 0;
        left: 0;
        right: 0;

        @media screen and (max-width: 767px) {
            display: none;
        }

        &::after {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
        }
    }
}

.content-more {
    &__body {
        overflow: hidden;
        height: 226px;
        position: relative;
        transition: height 0.3s ease;
        margin-bottom: 10px;

        &::after {
            content: '';
            position: absolute;
            bottom: 0;
            left: 0;
            right: 0;
            height: 150px;
            background: linear-gradient(to bottom, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 1) 90%);
            transition: opacity 0.3s ease;
        }

        &.open {
            height: 100%;

            &::after {
                opacity: 0;
            }
            
        }
    }

    &__button {
        position: relative;
        cursor: pointer;
        border-bottom: 1px solid #301410;

        &::before {
            content: '';
            position: absolute;
            top: 50%;
            transform: translateY(-50%) rotate(45deg);
            right: -20px;
            border-bottom: 1px solid #301410;
            border-right: 1px solid #301410;
            width: 9px;
            height: 9px;
            transition: 0.3s;
        }

        &.open {
            &::before {
                transform: translateY(-3%) rotate(-135deg);
            }
        }
    }
    
}

.pagination-block  {
    display: flex;
    flex-direction: column;
    align-items: center;

    &__list {
        margin-bottom: 40px;
    }
}

.share-button {
    .ya-share2 {
        .ya-share2__icon.ya-share2__icon_more {
            background-image: url('../img/image/share-icon.svg');
            background-size: 24px 24px;
            background-position: center;
            background-repeat: no-repeat;
        }

        .ya-share2__container_size_m {
            .ya-share2__link.ya-share2__link_more.ya-share2__link_more-button-type_short {
                background: transparent;
                padding: 0;
            }
        }
       

        li {
            padding: 0;
            margin: 0;
            &::before {
                display: none;
            }
        }
    }
}