.page-catalog {
    &__category-navigation {
        margin-bottom: 60px;

        @media screen and (max-width: 991px) {
            margin-bottom: 20px;
        }
    }
}

.filter-block {
    display: flex;
    flex-direction: column;
    gap: 30px;
    padding: 15px 35px;

    @media screen and (max-width: 991px) {
        display: none;
    }

    &__container {
        display: flex;
        flex-direction: column;
        gap: 30px;
    }

    &__list {
        display: flex;
        flex-direction: column;
        gap: 20px;
    }

    &__link {
        text-decoration: none;
        &--min {
            font-size: 16px;
        } 
    }
}

.filter-block-button-mobi {
    display: none;

    width: fit-content;
    gap: 10px;
    align-items: center;
    justify-content: center;
    font-size: 18px;
    @media screen and (max-width: 991px) {
        display: flex;
    }
    &__icon-svg {
        width: 24px;
        height: 24px;
    }
}

.filter-select-param {
    display: flex;
    gap: 24px;

    @media screen and (max-width: 767px) {
        position: relative;
        width: 100vw;
        left: -15px;
    }
   

    &__title {
        margin-top: 4px;
        white-space: nowrap;

        @media screen and (max-width: 767px) {
            display: none;
        }
    }

    &__list {
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        gap: 10px;

        @media screen and (max-width: 767px) {
            flex-wrap: nowrap;
            overflow: auto;
            padding: 0 15px;

            &::-webkit-scrollbar {
                display: none;
            }
        }
    }

    &__item {
        position: relative;
        border-radius: 20px;
        padding: 7px 45px 7px 15px;
        font-size: 14px;
        border: 1px solid #F4EFE9;
        white-space: nowrap;
    }

    &__close {
        position: absolute;
        width: 14px;
        height: 14px;
        top: 50%;
        right: 15px;
        cursor: pointer;
        transform: translateY(-50%);
        &:before, &:after {
            position: absolute;
            content: '';
            top: 50%;
            left: 50%;
            width: 100%;
            height: 1px;
            background-color: #301410;
            transform: translate(-50%, -50%) rotate(45deg);
        }
        &:after {
            transform: translate(-50%, -50%) rotate(-45deg);
        }
    }
}