.footer {
  margin-top: 100px;

  @media screen and (max-width: 767px) {
    margin-top: 80px;
  }

  &__copyright-block {
    padding: 20px 0;
    border-top: 2px solid #F4EFE9;
    display: flex;
    gap: 20px 40px;
    flex-wrap: wrap;
    font-size: 14px;
    @media screen and (max-width: 1024px) {
        flex-direction: column;
    }

    a {
        text-decoration: none;
    }
  }
}

.styles {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 10px 20px;

    @media screen and (max-width: 767px) {
        grid-template-columns: repeat(1, 1fr);
    }

    &__item {
        text-align: right;
        padding: 13px 56px 18px 56px;
        background-color: #F8E8D8;
        background-image: url('../img/image/styles_1.png');
        background-position: left center;
        background-repeat: no-repeat;
        background-size: cover;
        color: #301410;
        text-decoration: none;
        font-size: 24px;

        @media screen and (max-width: 767px) {
            padding: 18px 20px;
        }

        &:hover {
            color: #301410;
        }

        &:nth-child(1) {
            @media screen and (max-width: 767px) {
                background-position: -100px center;
            }
        }

        &:nth-child(2) {
            background-color: #FFDD9F;
            background-image: url('../img/image/styles_2.png');
        }

        &:nth-child(3) {
            background-color: #E9BDAD;
            background-image: url('../img/image/styles_3.png');
        }
    }
}

.footer-main-block {
    padding: 60px 0 20px 0;
    display: flex;
    gap: 20px;
    justify-content: space-between;

    @media screen and (max-width: 1024px) {
        flex-direction: column;
        gap: 0;
    }

    &__logo {
        max-width: 162px;
        display: block;
        margin-bottom: 40px;

        @media screen and (max-width: 1024px) {
            max-width: 122px;
            margin: 0 auto 40px auto;
        }
    }

    &__col {
        width: 100%;
        &:first-child {
            max-width: 336px;
            @media screen and (max-width: 1024px) {
                max-width: 100%;
            }
        }
        &:nth-child(2) {
            max-width: 610px;
            @media screen and (max-width: 1024px) {
                max-width: 100%;
            }
        }
        &:nth-child(3) {
            max-width: 314px;
            @media screen and (max-width: 1024px) {
                max-width: 100%;
            }
        }
    }

    &__subscription {
        &--desk {
            display: block;
            @media screen and (max-width: 1024px) {
                display: none;
            }
        }
        &--mobi {
            display: none;
            margin-bottom: 70px;
            @media screen and (max-width: 1024px) {
                display: block;
            }
        }
    }

    &__menu {
        @media screen and (max-width: 1024px) {
            margin-bottom: 30px;
        }
    }

    // &__menu-title {
    //     display: block;
    //     margin-bottom: 24px;
    // }

    // &__menu-list-item {
    //     padding: 0;
    //     margin-bottom: 14px;
    //     line-height: normal;

    //     &::before {
    //         display: none;
    //     }
    // }

    // &__menu-list-link {
    //     font-size: 14px;
    //     text-decoration: none;
    // }

    &__tel {
        display: block;
        text-decoration: none;
        margin-bottom: 20px;
        font-size: 24px;

        @media screen and (max-width: 1024px) {
            font-size: 20px;
        }
    }

    &__social {
        display: flex;
        gap: 18px;
        margin-bottom: 20px;
    }

    &__social-item-svg {
        width: 42px;
        height: 40px;
    }

    &__payment-title {
        display: block;
        margin-bottom: 10px;

        @media screen and (max-width: 1024px) {
            font-size: 16px;
        }
    }

    &__payment-list {
        display: flex;
        gap: 12px;
        margin-bottom: 10px;
    }
    
    &__payment-img {
        max-width: 48px;
        width: 100%;
    }

    &__payment-subtitle {
        font-size: 12px;
    }
}

.footer-subscription {
    &__title {
        display: block;
        margin-bottom: 10px;
        font-size: 20px;

        @media screen and (max-width: 1024px) {
            font-size: 18px;
        }
    }

    &__subtitle {
        display: block;
        margin-bottom: 20px;
        color: #BEB1A6;
        font-size: 14px;
    }

    &__input {
        padding: 13px 25px;
        font-family: $font;
        font-size: 16px;
        color: #301410;
        border-radius: 40px;
        border: 1px solid #F4EFE9;
        width: 100%;

        &::placeholder {
            color: #301410;
        }
    }
}

.menu-footer {
    width: 100%;
    background-color: #ffffff;
    min-height: 100%;

    &__menu {
        display: flex;
        justify-content: space-between;
        gap: 20px;

        @media screen and (max-width: 1024px) {
            flex-direction: column;
            gap: 0;
        }
    }

    &__item {
        width: 100%;
        padding: 0;
        margin: 0;

        &::before {
            display: none;
        }
    }

    &__item-title {
        position: relative;
        text-decoration: none;
        color: #260B00;
        display: block;
        margin-bottom: 24px;

        @media screen and (max-width: 1024px) {
            padding: 10px 15px;
            font-size: 16px;
            margin-bottom: 0;
        }

        &--trigger {
            @media screen and (max-width: 1024px)  {
                cursor: pointer;
            }
        }

        &.open {
            .menu-footer {
                &__arrow {
                    transform: translateY(-50%) rotate(90deg);
                }
            }
        }
    }

    &__item-block {
        @media screen and (max-width: 1024px) {
            display: none;
            overflow: hidden;

            .menu-footer {
                &__list {
                    padding: 20px 15px 16px 15px;
                }
            }
        }
    }

    &__elem-lvl1, &__elem-lvl2  {
        color: #260B00;
        text-decoration: none;

        &:hover {
            color: #260B00;
        }
    }

    &__item-content {
        .menu-footer {
            &__item {
                padding: 0;
                margin-bottom: 14px;
                line-height: normal;
                font-size: 14px;
                
                &:last-child {
                    margin-bottom: 0;
                }
            }
        }

        @media screen and (max-width: 1024px) {
            width: 100%;
            border-bottom: 1px solid #F4EFE9;
        }
    }

    &__arrow {
        display: none;
        position: absolute;
        top: 50%;
        right: 15px;
        transform: translateY(-50%) rotate(270deg);
        width: 20px;
        height: 20px;
        align-items: center;
        justify-content: center;
        transition: 0.3s;

        &-svg {
            width: 8px;
            height: 15px;
        }

        @media screen and (max-width: 1024px) {
            display: flex;
        }
    }
}